'use client';

import { useState } from 'react';
import Image from 'next/image';
import { useCart } from '@/context/CartContext';
import { formatPrice } from '@/utils/formatCurrency';

const products = [
  {
    id: 1,
    name: 'Premium Dumbbell Set',
    price: 299.99,
    image: '/images/dumbbell-set.jpg',
    category: 'Equipment',
    description: 'Professional grade dumbbell set with rack, perfect for home gyms.',
  },
  {
    id: 2,
    name: 'Resistance Bands Pack',
    price: 49.99,
    image: '/images/resistance-bands.jpg',
    category: 'Accessories',
    description: 'Set of 5 resistance bands with different tension levels.',
  },
  {
    id: 3,
    name: 'Whey Protein Powder',
    price: 59.99,
    image: '/images/protein-powder.jpg',
    category: 'Supplements',
    description: 'Premium whey protein powder for muscle recovery and growth.',
  },
  {
    id: 4,
    name: 'Performance T-Shirt',
    price: 29.99,
    image: '/images/tshirt.jpg',
    category: 'Clothing',
    description: 'Moisture-wicking performance t-shirt for intense workouts.',
  },
  // Add more products as needed
];

const categories = ['All', 'Equipment', 'Accessories', 'Supplements', 'Clothing'];
const sortOptions = ['Price: Low to High', 'Price: High to Low', 'Name: A to Z', 'Name: Z to A'];

export default function ProductsPage() {
  const [selectedCategory, setSelectedCategory] = useState('All');
  const [sortBy, setSortBy] = useState(sortOptions[0]);
  const [searchQuery, setSearchQuery] = useState('');
  const { addToCart } = useCart();

  // Filter products based on category and search query
  const filteredProducts = products.filter((product) => {
    const matchesCategory = selectedCategory === 'All' || product.category === selectedCategory;
    const matchesSearch = product.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      product.description.toLowerCase().includes(searchQuery.toLowerCase());
    return matchesCategory && matchesSearch;
  });

  // Sort products based on selected option
  const sortedProducts = [...filteredProducts].sort((a, b) => {
    switch (sortBy) {
      case 'Price: Low to High':
        return a.price - b.price;
      case 'Price: High to Low':
        return b.price - a.price;
      case 'Name: A to Z':
        return a.name.localeCompare(b.name);
      case 'Name: Z to A':
        return b.name.localeCompare(a.name);
      default:
        return 0;
    }
  });

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      {/* Filters and Search */}
      <div className="mb-8 space-y-4">
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
          <h1 className="text-3xl font-bold text-teal-800">Products</h1>
          <div className="flex flex-col sm:flex-row gap-4">
            <select
              value={sortBy}
              onChange={(e) => setSortBy(e.target.value)}
              className="border border-teal-200 rounded-md px-4 py-2 focus:border-orange-400 focus:ring focus:ring-orange-200 focus:ring-opacity-50"
            >
              {sortOptions.map((option) => (
                <option key={option} value={option}>
                  {option}
                </option>
              ))}
            </select>
            <input
              type="text"
              placeholder="Search products..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="border border-teal-200 rounded-md px-4 py-2 focus:border-orange-400 focus:ring focus:ring-orange-200 focus:ring-opacity-50"
            />
          </div>
        </div>
        <div className="flex flex-wrap gap-2">
          {categories.map((category) => (
            <button
              key={category}
              onClick={() => setSelectedCategory(category)}
              className={`px-4 py-2 rounded-full text-sm font-medium ${selectedCategory === category
                ? 'bg-orange-400 text-teal-900'
                : 'bg-teal-50 text-teal-700 hover:bg-teal-100'
                }`}
            >
              {category}
            </button>
          ))}
        </div>
      </div>

      {/* Products Grid */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-8">
        {sortedProducts.map((product) => (
          <div
            key={product.id}
            className="bg-white rounded-lg shadow-md overflow-hidden hover:shadow-lg transition-shadow duration-300 border border-teal-100"
          >
            <div className="relative h-48">
              <Image
                src={product.image}
                alt={product.name}
                fill
                className="object-cover"
              />
            </div>
            <div className="p-4">
              <span className="text-sm text-orange-500 font-medium">
                {product.category}
              </span>
              <h3 className="text-lg font-semibold text-teal-800 mt-1">
                {product.name}
              </h3>
              <p className="text-teal-600 mt-2 text-sm line-clamp-2">
                {product.description}
              </p>
              <p className="text-teal-900 font-semibold mt-2">{formatPrice(product.price)}</p>
              <button
                onClick={() => {
                  addToCart(product);
                  // Optional: Add a toast notification here
                }}
                className="w-full mt-4 bg-orange-400 text-teal-900 py-2 rounded-md hover:bg-orange-500 transition-colors duration-300 flex items-center justify-center gap-2"
              >
                <svg
                  className="w-5 h-5"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M3 3h2l.4 2M7 13h10l4-8H5.4M7 13L5.4 5M7 13l-2.293 2.293c-.63.63-.184 1.707.707 1.707H17m0 0a2 2 0 100 4 2 2 0 000-4zm-8 2a2 2 0 11-4 0 2 2 0 014 0z"
                  />
                </svg>
                Add to Cart
              </button>
            </div>
          </div>
        ))}
      </div>

      {/* Empty State */}
      {sortedProducts.length === 0 && (
        <div className="text-center py-12">
          <h3 className="text-lg font-medium text-teal-800 mb-2">No products found</h3>
          <p className="text-teal-600">
            Try adjusting your search or filter criteria
          </p>
        </div>
      )}
    </div>
  );
} 